import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
// import { Link } from "react-router-dom";
import moment from "moment";
import NumberFormat from "../../../App/NumberFormat";

import notification from "../../../../components/notification";
import {
  Empty,
  Collapse,
  Select,
  Row,
  Col,
  Divider,
  Spin,
  // DatePicker,
  // Tooltip,
  // Table
} from "antd";

// import Popconfirms from "../../../../components/feedback/popconfirm";
import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
// import Scrollbars from "../../../../components/utility/customScrollBar";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/report/fixedAssetDetailReport/actions";
import TableWrapper from "../../../Tables/antTables/antTable.style";
import CardWrapper, {
  Box,
  StatusTag,
  customCollapseStyle,
  // StatusBadge,
  // TransNo
} from "../../../App/app.style";

import Print from "./print";
//import PrintWrapper from "../../../App/PrintWrapper";
// import ReactToPrint from "react-to-print";
// import ButtonPrint from "../../../App/ButtonPrint";
// import ExcelWrapper from "../../../App/ExcelWrapper";
// import PDFWrapper from "../../../App/PDFWrapper";
// import { getTransactions } from "../../../../redux/purchase/purchaseOrder/saga";
import getLanguage from "../../../App/MyLanguange";

const Panel = Collapse.Panel;
const Option = Select.Option;
// const { RangePicker } = DatePicker;

const redirectTo = "/dashboard/report-menus";

class FixedAssetDetailReport extends Component {
  state = {
    filter: {
      id_location: null,
      id_asset_category: null,
      id_asset_type: null,
      id_department: null,
      id_fixed_asset: null,
    }
  };

  // Get All data diawal
  componentDidMount() {
    const { getFixedAssetDetailReports } = this.props;
    getFixedAssetDetailReports();
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.fixedAssetDetailReport.error) {
      notification("error", this.props.fixedAssetDetailReport.error);
      this.props.clearError();
    }

    // Display success notif
    if (this.props.fixedAssetDetailReport.success) {
      notification("success", this.props.fixedAssetDetailReport.success);
      this.props.clearSuccess();
    }
  }

  //handle Search Value
  handleSearchValue = e => {
    const value = e.target.value;
    this.setState({ search: value });
  };

  //   Handle filter change
  handleFilterChange = (name, value) => {
    if (name === "date" && value[0] && value[1]) {
      const start = value[0].format("YYYY-MM-DD HH:mm:ss");
      const end = value[1].format("YYYY-MM-DD HH:mm:ss");
      // console.log(start, end);
      this.setState({
        filter: {
          ...this.state.filter,
          start_date: start,
          end_date: end
        }
      });
    } else if (name === "date" && !value[0] && !value[1]) {
      this.setState({
        filter: {
          ...this.state.filter,
          start_date: null,
          end_date: null
        }
      });
    }
    else if (name === "id_fixed_asset") {
      this.setState({
        filter: {
          ...this.state.filter,
          [name]: value,
        }
      });
    }
    else if (name === "id_asset_category") {
      this.setState({
        filter: {
          ...this.state.filter,
          [name]: value,
          id_asset_type: null,
          id_fixed_asset: null
        }
      });
    }
    else {
      this.setState({
        filter: {
          ...this.state.filter,
          [name]: value,
          id_fixed_asset: null
        }
      });
    }
  };

  filterValue = () => {
    const { filter } = this.state;
    let string = "";

    if (filter.id_location) {
      string += "id_location=" + filter.id_location;
    }
    if (filter.id_department) {
      string
        ? (string += "&id_department=" + filter.id_department)
        : (string += "id_department=" + filter.id_department);
    }
    if (filter.id_asset_category) {
      string
        ? (string += "&id_asset_category=" + filter.id_asset_category)
        : (string += "id_asset_category=" + filter.id_asset_category);
    }
    if (filter.id_asset_type) {
      string
        ? (string += "&id_asset_type=" + filter.id_asset_type)
        : (string += "id_asset_type=" + filter.id_asset_type);
    }
    if (filter.id_fixed_asset) {
      string
        ? (string += "&id_fixed_asset=" + filter.id_fixed_asset)
        : (string += "id_fixed_asset=" + filter.id_fixed_asset);
    }

    return string;
  };

  // Handle filter
  handleFilter = () => {
    const { getFixedAssetDetailReports } = this.props;
    //const { current_page } = this.props.fixedAssetDetailReport;
    let filter = this.filterValue();

    this.setState({ search: null });
    getFixedAssetDetailReports(1, null, filter, this.state.filter);
  };

  // Handle Reset Filter
  handleResetFilter = () => {
    const { getFixedAssetDetailReports } = this.props;
    this.setState({
      filter: {
        id_location: null,
        id_asset_category: null,
        id_asset_type: null,
        id_department: null,
        id_fixed_asset: null,
      }
    });
    getFixedAssetDetailReports();
  };



  render() {
    const expenditureColumns = [
      {
        title: "No.",
        dataIndex: "index_no",
        width: "5%",
        align: "center",
        render: text => {
          if (isNaN(text)) {
            return {
              children: text,
              props: {
                colSpan: 3,
              }
            };
          }
          else {
            return text
          }
        }
      },
      {
        title: getLanguage({ id: "accountCode" }),
        dataIndex: "account_code",
        width: "30%",
        render: (text, row) => {
          if (isNaN(row.index_no)) {
            return {
              props: {
                colSpan: 0
              }
            };
          }
          else {
            return text
          }
        }
      },
      {
        title: getLanguage({ id: "accountName" }),
        dataIndex: "account_name",
        width: "45%",
        render: (text, row) => {
          if (isNaN(row.index_no)) {
            return {
              props: {
                colSpan: 0
              }
            };
          }
          else {
            return text
          }
        }
      },
      {
        title: getLanguage({ id: "baseAmount" }),
        dataIndex: "amount",
        width: "20%",
        align: "center",
        render: text => <NumberFormat value={text} />
      },
    ];

    // const noteColumns = [
    //   {
    //     title: getLanguage({ id: "date" }),
    //     dataIndex: "note_date",
    //     rowKey: "note_date",
    //     width: "15%",
    //     render: text => <span>{moment(text).format("DD MMMM YYYY")}</span>
    //   },
    //   {
    //     title: "Note",
    //     dataIndex: "note",
    //     rowKey: "note",
    //     width: "60%",
    //     render: text => {
    //       if (text && text.length > 75) {
    //         return (
    //           <Tooltip placement="bottom" title={text}>
    //             {text.substring(0, 75) + "..."}
    //           </Tooltip>
    //         );
    //       }
    //       else {
    //         return text;
    //       }
    //     }
    //   },
    //   {
    //     title: getLanguage({ id: "createdBy" }),
    //     dataIndex: "created_by",
    //     rowKey: "created_by",
    //     width: "25%",
    //   },
    // ];

    const maintenanceColumns = [
      {
        title: getLanguage({ id: "date" }),
        dataIndex: "service_date",
        rowKey: "service_date",
        width: "10%",
        render: text => <span>{moment(text).format("DD MMMM YYYY")}</span>
      },
      {
        title: getLanguage({ id: "sidebar.assetServiceType" }),
        dataIndex: "asset_service_type",
        rowKey: "asset_service_type",
        width: "30%",
        render: (text, row) => {
          const { asset_service_type } = row;
          return <span>
            {asset_service_type.description + " " +
              parseFloat(asset_service_type.interval_value) + " " +
              asset_service_type.interval_unit}
          </span>
        }
      },
      {
        title: getLanguage({ id: "serviceParameter" }),
        dataIndex: "service_parameter",
        rowKey: "service_parameter",
        width: "25%",
      },
      // {
      //   title: "Service Note",
      //   dataIndex: "service_note",
      //   rowKey: "service_note",
      //   width: "35%",
      //   render: text => {
      //     if (text && text.length > 50) {
      //       return (
      //         <Tooltip placement="bottom" title={text}>
      //           {text.substring(0, 50) + "..."}
      //         </Tooltip>
      //       );
      //     }
      //     else {
      //       return text;
      //     }
      //   }
      // },
      {
        title: getLanguage({ id: "createdBy" }),
        dataIndex: "created_by",
        rowKey: "created_by",
        width: "15%",
      },
    ];

    const depreciationColumns = [
      {
        title: getLanguage({ id: "date" }),
        dataIndex: "transaction_date",
        rowKey: "transaction_date",
        width: "20%",
        render: text => <span>{moment(text).format("DD MMMM YYYY HH:mm:ss")}</span>
      },
      {
        title: getLanguage({ id: "depreciationType" }),
        dataIndex: "depreciation_type",
        rowKey: "depreciation_type",
        width: "40%",
        align: "center",
        render: text => {
          let className;
          let display;
          switch (text) {
            case 1:
              className = "primary";
              display = getLanguage({ id: "fixedAssets.tab.revaluation" });
              break;
            case 2:
              className = "ordered";
              display = getLanguage({ id: "periods.pageHeader" });
              break;
            default:
              className = "primary";
              display = getLanguage({ id: "fixedAssets.tab.revaluation" });
              break;
          }

          return <StatusTag className={className}>{display}</StatusTag>;
        }
      },
      {
        title: getLanguage({ id: "baseAmount" }),
        dataIndex: "amount",
        rowKey: "amount",
        width: "40%",
        align: "right",
        render: text => <NumberFormat value={parseFloat(text)} />
      },
    ]

    const disposalColumns = [
      {
        title: getLanguage({ id: "date" }),
        dataIndex: "transaction_date",
        rowKey: "transaction_date",
        width: "25%",
        render: text => <span>{moment(text).format("DD MMMM YYYY HH:mm:ss")}</span>
      },
      {
        title: getLanguage({ id: "transactionType" }),
        dataIndex: "transaction_type",
        rowKey: "transaction_type",
        width: "25%",
        align: "center",
        render: text => {
          let className;
          let display;
          switch (text) {
            case 1:
              className = "primary";
              display = getLanguage({ id: "valueAdd" });
              break;
            case 2:
              className = "ordered";
              display = getLanguage({ id: "fixedAssets.tab.revaluation" });
              break;
            case 3:
              className = "invoiced";
              display = getLanguage({ id: "fixedAssetSale" });
              break;
            default:
              className = "primary";
              display = getLanguage({ id: "valueAdd" });
              break;
          }

          return <StatusTag className={className}>{display}</StatusTag>;
        }
      },
      {
        title: getLanguage({ id: "qtyChange" }),
        dataIndex: "qty",
        rowKey: "qty",
        width: "25%",
        align: "right",
        render: text => <NumberFormat value={text} />
      },
      {
        title: getLanguage({ id: "baseAmount" }),
        dataIndex: "amount",
        rowKey: "amount",
        width: "25%",
        align: "right",
        render: text => <NumberFormat value={text} />
      },
    ];


    const transferColumns = [
      {
        title: getLanguage({ id: "date" }),
        dataIndex: "transaction_date",
        rowKey: "transaction_date",
        width: "20%",
        render: text => <span>{moment(text).format("DD MMMM YYYY HH:mm:ss")}</span>
      },
      {
        title: getLanguage({ id: "itemTransfers.fromLocation" }),
        dataIndex: "from_location_name",
        rowKey: "from_location_name",
        width: "30%",
      },
      {
        title: getLanguage({ id: "itemTransfers.toLocation" }),
        dataIndex: "to_location_name",
        rowKey: "to_location_name",
        width: "30%",
      },
      {
        title: getLanguage({ id: "qtyTransferred" }),
        dataIndex: "qty",
        rowKey: "qty",
        width: "20%",
        align: "right",
        render: text => <NumberFormat value={text} />
      },
    ]
    // Data setup
    const {
      locations,
      departments,
      asset_types,
      asset_categories,
      fixed_assets,
      loading,
      loadingForeign,
      filter,
      filter_select
    } = this.props.fixedAssetDetailReport ? this.props.fixedAssetDetailReport : [];
    //console.log(filter);'



    const data = this.props.fixedAssetDetailReport.fixed_asset_detail_reports
      ? this.props.fixedAssetDetailReport.fixed_asset_detail_reports
      : null
    // console.log(data);
    const { user } = this.props.auth;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];

    let fixed_asset_expenditures = [];
    let fixed_asset_maintenances = data && data.fixed_asset_maintenances ? data.fixed_asset_maintenances : [];
    let fixed_asset_depreciations = data && data.fixed_asset_depreciations ? data.fixed_asset_depreciations : [];
    let fixed_asset_disposals = data && data.fixed_asset_revaluations ? data.fixed_asset_revaluations : [];
    let fixed_asset_tranfers = data && data.fixed_asset_tranfers ? data.fixed_asset_tranfers : [];



    if (data && data.fixed_asset_expenditures && data.fixed_asset_expenditures.length > 0) {
      fixed_asset_expenditures = [...data.fixed_asset_expenditures]
      // console.log(data)
      let asset_value = 0
      fixed_asset_expenditures.map((x) => {
        asset_value += parseFloat(x.amount)
      })

      fixed_asset_expenditures.push({
        id_fixed_asset_expenditure: 1,
        index_no: "Total",
        account_name: "Total",
        amount: asset_value
      })
    }

    const RowModif = (props) =>
      <Row style={{ fontSize: "14px", marginBottom: "5px" }}>
        {
          props.left
            ?
            <>
              <Col span={4}><strong><IntlMessages id={props.leftLabel}></IntlMessages></strong></Col>
              <Col span={1}>:</Col>
              <Col span={7}>{props.leftData}</Col>
            </>
            : <Col span={12} />

        }
        {
          props.right
            ?
            <>
              <Col span={4}><strong><IntlMessages id={props.rightLabel}></IntlMessages></strong></Col>
              <Col span={1}>:</Col>
              <Col span={7}>{props.rightData}</Col>
            </>
            : null
        }
      </Row>



    //console.log(this.props);
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader
            title={<IntlMessages id="fixedAssetDetailReports.pageHeader" />}
            marginBottom={true}
          >
            <Print
              expenditureColumns={expenditureColumns}
              depreciationColumns={depreciationColumns}
              disposalColumns={disposalColumns}
              transferColumns={transferColumns}
              maintenanceColumns={maintenanceColumns}
              fixed_asset_expenditures={fixed_asset_expenditures}
              filter={filter_select}
              loading={loading}
              data={data}
            />


            <Button
              style={{ marginLeft: "5px" }}
              onClick={() => this.props.history.replace(redirectTo)}
            >
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>

          <div className="isoFilter">
            <Collapse style={customCollapseStyle} defaultActiveKey={["1"]}>
              <Panel header={<IntlMessages id="select.fixedAsset" />} key="1">
                <Row
                  justify="space-between"
                  type="flex"
                  style={{ marginBottom: "10px" }}
                >
                  <Col span={12}>
                    <Col span={12}>
                      <label>
                        <IntlMessages id="locations.pageHeader" /> <span style={{ color: "red" }}>*</span>
                      </label>
                      <br />
                      <Select
                        showSearch
                        onChange={this.handleFilterChange.bind(
                          this,
                          "id_location"
                        )}
                        value={
                          this.state.filter.id_location
                            ? this.state.filter.id_location
                            : []
                        }
                        style={{ width: "95%" }}
                        placeholder={<IntlMessages id="select.location" />}
                        optionFilterProp="children"
                        filterOption={true}
                        loading={loadingForeign}
                        allowClear={true}
                      //showArrow={false}
                      >
                        {locations.map((data, key) => (
                          <Option key={key} value={data.id_location}>
                            {`${data.location_code} (${data.location_name})`}
                          </Option>
                        ))}
                      </Select>
                    </Col>
                    <Col span={12}>
                      <label>
                        <IntlMessages id="departments.pageHeader" />
                      </label>
                      <br />
                      <Select
                        showSearch
                        onChange={this.handleFilterChange.bind(
                          this,
                          "id_department"
                        )}
                        value={
                          this.state.filter.id_department
                            ? this.state.filter.id_department
                            : []
                        }
                        style={{ width: "90%" }}
                        placeholder={<IntlMessages id="select.department" />}
                        optionFilterProp="children"
                        filterOption={true}
                        loading={loadingForeign}
                        allowClear={true}
                      //showArrow={false}
                      >
                        {departments.map((data, key) => (
                          <Option key={key} value={data.id_department}>
                            {`${data.department_code} (${data.department_name})`}
                          </Option>
                        ))}
                      </Select>
                    </Col>
                  </Col>
                  <Col span={12}>
                    <label>
                      <IntlMessages id="fixedAssets.pageHeader" /> <span style={{ color: "red" }}>*</span>
                    </label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.handleFilterChange.bind(
                        this,
                        "id_fixed_asset"
                      )}
                      value={
                        this.state.filter.id_fixed_asset
                          ? this.state.filter.id_fixed_asset
                          : []
                      }
                      style={{ width: "100%" }}
                      placeholder={this.state.filter.id_location ? (<IntlMessages id="select.fixedAsset" />) : (<IntlMessages id="noLocation" />) }
                      optionFilterProp="children"
                      filterOption={true}
                      loading={loadingForeign}
                      allowClear={true}
                      notFoundContent={
                        this.state.filter.id_location
                          ?
                          (
                            <Empty description={<IntlMessages id="noData" />} />
                          )
                          : (
                            <Empty
                              description={
                                <IntlMessages id="noLocation" />
                              }
                            />
                          )
                      }
                    >
                      {fixed_assets.map((data, key) => (
                        this.state.filter.id_location === data.id_location &&
                          (this.state.filter.id_department
                            ? this.state.filter.id_department === data.id_department
                            : data.id_department !== null
                          )
                          &&
                          (this.state.filter.id_asset_category
                            ? this.state.filter.id_asset_category === data.id_asset_category
                            : data.id_asset_category !== null
                          )
                          &&
                          (this.state.filter.id_asset_type
                            ? this.state.filter.id_asset_type === data.id_asset_type
                            : data.id_asset_type !== null
                          )
                          ?
                          (
                            <Option key={key} value={data.id_fixed_asset}>
                              {`${data.fixed_asset_code} (${data.fixed_asset_name})`}
                            </Option>
                          )
                          : null
                      ))}
                    </Select>
                  </Col>

                </Row>
                <Row
                  justify="space-between"
                  type="flex"
                  style={{ marginBottom: "24px" }}
                >
                  <Col span={12}>
                    <Col span={12}>
                      <label>
                        <IntlMessages id="assetCategories.pageHeader" />
                      </label>
                      <br />
                      <Select
                        showSearch
                        onChange={this.handleFilterChange.bind(
                          this,
                          "id_asset_category"
                        )}
                        value={
                          this.state.filter.id_asset_category
                            ? this.state.filter.id_asset_category
                            : []
                        }
                        style={{ width: "95%" }}
                        placeholder={<IntlMessages id="select.assetCategory" />}
                        optionFilterProp="children"
                        filterOption={true}
                        loading={loadingForeign}
                        allowClear={true}
                      //showArrow={false}
                      >
                        {asset_categories.map((data, key) => (
                          <Option key={key} value={data.id_asset_category}>
                            {`${data.asset_category_code} (${data.asset_category_name})`}
                          </Option>
                        ))}
                      </Select>
                    </Col>
                    <Col span={12}>
                      <label>
                        <IntlMessages id="assetTypes.pageHeader" />
                      </label>
                      <br />
                      <Select
                        showSearch
                        onChange={this.handleFilterChange.bind(
                          this,
                          "id_asset_type"
                        )}
                        value={
                          this.state.filter.id_asset_type
                            ? this.state.filter.id_asset_type
                            : []
                        }
                        style={{ width: "90%" }}
                        placeholder={<IntlMessages id="select.assetCategoryFirst" />}
                        optionFilterProp="children"
                        filterOption={true}
                        loading={loadingForeign}
                        allowClear={true}
                      //showArrow={false}
                      >
                        {asset_types.map((data, key) => (
                          this.state.filter.id_asset_category === data.id_asset_category
                            ?
                            (
                              <Option key={key} value={data.id_asset_type}>
                                {`${data.asset_type_code} (${data.asset_type_name})`}
                              </Option>
                            )
                            : null
                        ))}
                      </Select>
                    </Col>
                  </Col>
                  <Col span={12}>
                    <Row justify="start" gutter={1} type="flex">
                      <Col span={8} style={{ marginTop: "24px" }}>
                        <Button
                          size="small"
                          type="primary"
                          style={{ marginRight: "10px" }}
                          onClick={this.handleFilter}
                          disabled={this.state.filter.id_fixed_asset ? false : true}
                        >
                          <IntlMessages id="button.select" />
                        </Button>
                        <Button onClick={this.handleResetFilter} size="small">
                          <IntlMessages id="filter.reset" />
                        </Button>
                      </Col>
                    </Row>
                  </Col>
                </Row>
              </Panel>
            </Collapse>
          </div>

          <CardWrapper>
            {!filter ? (
              <Empty description={<IntlMessages id="noLocationFixedAsset" />} />
            ) : !data &&
              !this.props.fixedAssetDetailReport.loading ? (
                  <Empty
                    description={
                      <IntlMessages id="fixedAssetDetailReports.table.noData" />
                    }
                  />
                ) :
                this.props.fixedAssetDetailReport.loading
                  ? <Spin />
                  :
                  (
                    <div>
                      <Divider orientation="left" style={{ fontSize: 14 }}>
                        <IntlMessages id="details" />
                      </Divider>

                      <RowModif
                        left={true}
                        leftLabel="assetCategories.pageHeader"
                        leftData={data.asset_category.asset_category_name}
                        right={true}
                        rightLabel="assetTypes.add.depreciationMethod"
                        rightData={
                          data.depreciation_method === 1
                            ? <IntlMessages id="Non Depreciable" />
                            : data.depreciation_method === 3
                              ? <IntlMessages id="Double Declining" />
                              : <IntlMessages id="Straight Line" />
                        }
                      />
                      <RowModif
                        left={true}
                        leftLabel="assetTypes.pageHeader"
                        leftData={data.asset_type.asset_type_name}
                        right={true}
                        rightLabel="estimatedlife"
                        rightData={data.estimated_life_year + " " + getLanguage({id : "year"}) + " " + data.estimated_life_month + " " + getLanguage({id : "month"})}
                      />
                      <RowModif
                        left={true}
                        leftLabel="locations.pageHeader"
                        leftData={data.location.location_name}
                        right={true}
                        rightLabel="depreciationRate"
                        rightData={<><NumberFormat value={data.depreciation_rate} />{"%"}</>}
                      />
                      <RowModif
                        left={true}
                        leftLabel="departments.pageHeader"
                        leftData={data.department.department_name}
                        right={true}
                        rightLabel="intangible"
                        rightData={data.intangible === 1
                          ? <IntlMessages id="yes" />
                          : <IntlMessages id="no" />}
                      />
                      <RowModif
                        left={true}
                        leftLabel="fixedAssetCode"
                        leftData={data.fixed_asset_code}
                        right={true}
                        rightLabel="assetTypes.add.assetAccount"
                        rightData={data.account_code_asset + " (" + data.account_name_asset + ")"}
                      />
                      <RowModif
                        left={true}
                        leftLabel="fixedAssetName"
                        leftData={data.fixed_asset_name}
                        right={true}
                        rightLabel="assetTypes.add.depreciationAccount"
                        rightData={data.account_code_depreciation + " (" + data.account_name_depreciation + ")"}

                      />
                      <RowModif
                        left={true}
                        leftLabel="acquisitionDate"
                        leftData={moment(data.acquisition_date).format("DD MMMM YYYY HH:mm:ss")}
                        right={true}
                        rightLabel="assetTypes.add.expenseAccount"
                        rightData={data.account_code_expense + " (" + data.account_name_expense + ")"}
                      />
                      <RowModif
                        left={true}
                        leftLabel="usageDate"
                        leftData={moment(data.usage_date).format("DD MMMM YYYY")}
                        right={true}
                        rightLabel="quantity"
                        rightData={<NumberFormat value={data.qty} />}

                      />
                      <RowModif
                        left={true}
                        leftLabel="depreciationEndDate"
                        leftData={moment(data.depreciation_end_date).format("DD MMMM YYYY")}
                        right={true}
                        rightLabel="monthlyDepreciation"
                        rightData={<NumberFormat value={data.monthly_depreciation} />}
                      />
                      <RowModif
                        right={true}
                        rightLabel="valuePerQuantity"
                        rightData={
                          data.qty > 0
                            ? <NumberFormat value={parseFloat(data.book_value / data.qty)} />
                            : 0
                        }
                      />
                      <RowModif
                        right={true}
                        rightLabel="assetValue"
                        rightData={<NumberFormat value={data.asset_value} />}
                      />
                      <RowModif
                        right={true}
                        rightLabel="accumulationOfDepreciation"
                        rightData={<NumberFormat value={data.depreciation_value} />}
                      />
                      <RowModif
                        right={true}
                        rightLabel="bookValue"
                        rightData={<NumberFormat value={data.book_value} />}
                      />


                      <Divider orientation="left" style={{ fontSize: 14 }}>
                        <IntlMessages id="expenditureDetails" />
                      </Divider>

                      <div style={{ padding: "0 20px 20px" }} >
                        <TableWrapper
                          bordered={true}
                          dataSource={fixed_asset_expenditures}
                          columns={expenditureColumns}
                          className="invoiceListTable"
                          loading={loading}
                          pagination={false}
                        />
                      </div>

                      {
                        fixed_asset_depreciations.length > 0
                          ?
                          (
                            <>
                              <Divider orientation="left" style={{ fontSize: 14 }}>
                                <IntlMessages id="fixedAssets.tab.depreciation" />
                              </Divider>

                              <div style={{ padding: "0 20px 20px" }} >
                                <TableWrapper
                                  bordered={true}
                                  dataSource={fixed_asset_depreciations}
                                  columns={depreciationColumns}
                                  className="invoiceListTable"
                                  loading={loading}
                                  pagination={false}
                                />
                              </div>
                            </>
                          )
                          : null
                      }

                      {
                        fixed_asset_disposals.length > 0
                          ?
                          (
                            <>
                              <Divider orientation="left" style={{ fontSize: 14 }}>
                                <IntlMessages id="fixedAssets.tab.revaluation" />
                              </Divider>

                              <div style={{ padding: "0 20px 20px" }} >
                                <TableWrapper
                                  bordered={true}
                                  dataSource={fixed_asset_disposals}
                                  columns={disposalColumns}
                                  className="invoiceListTable"
                                  loading={loading}
                                  pagination={false}
                                />
                              </div>
                            </>
                          )
                          : null
                      }

                      {
                        fixed_asset_tranfers.length > 0
                          ?
                          (
                            <>
                              <Divider orientation="left" style={{ fontSize: 14 }}>
                                <IntlMessages id="fixedAssets.tab.transfer" />
                              </Divider>

                              <div style={{ padding: "0 20px 20px" }} >
                                <TableWrapper
                                  bordered={true}
                                  dataSource={fixed_asset_tranfers}
                                  columns={transferColumns}
                                  className="invoiceListTable"
                                  loading={loading}
                                  pagination={false}
                                />
                              </div>
                            </>
                          )
                          : null
                      }

                      {
                        fixed_asset_maintenances.length > 0
                          ?
                          (
                            <>
                              <Divider orientation="left" style={{ fontSize: 14 }}>
                                <IntlMessages id="fixedAssets.tab.maintenances" />
                              </Divider>

                              <div style={{ padding: "0 20px 20px" }} >
                                <TableWrapper
                                  bordered={true}
                                  dataSource={fixed_asset_maintenances}
                                  columns={maintenanceColumns}
                                  className="invoiceListTable"
                                  loading={loading}
                                  pagination={false}
                                />
                              </div>
                            </>
                          )
                          : null
                      }








                    </div>
                  )}
          </CardWrapper>
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    fixedAssetDetailReport: state.fixedAssetDetailReport,
    auth: state.auth
  };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(FixedAssetDetailReport);
