import React, { Component } from "react";
import { Row, Col, Divider } from "antd";

import IntlMessages from "../../../../components/utility/intlMessages";
import PrintTable from "../../../Tables/antTables/PrintTable.style";
import PrintTransaction, {
  PrintField,
  FieldCol
} from "../../../App/PrintTransaction";
import ReactToPrint from "react-to-print";
import ButtonPrint from "../../../App/ButtonPrint";
import moment from "moment";
import { Signature } from "../../../App/app.style";
import NumberFormat from "../../../App/NumberFormat";
import getLanguage from "../../../App/MyLanguange";
import { getFileName } from "../../../../helpers/helper";
import { siteConfig } from "../../../../settings";

class Print extends Component {
  render() {
    const data = this.props.data ? this.props.data : [];
    const {
      expenditureColumns,
      depreciationColumns,
      disposalColumns,
      transferColumns,
      maintenanceColumns,
      fixed_asset_expenditures
    } = this.props;

    let fixed_asset_maintenances =
      data && data.fixed_asset_maintenances
        ? data.fixed_asset_maintenances
        : [];
    let fixed_asset_depreciations =
      data && data.fixed_asset_depreciations
        ? data.fixed_asset_depreciations
        : [];
    let fixed_asset_disposals =
      data && data.fixed_asset_revaluations
        ? data.fixed_asset_revaluations
        : [];
    let fixed_asset_tranfers =
      data && data.fixed_asset_tranfers ? data.fixed_asset_tranfers : [];

    const { loading } = this.props;
    const leftSide = [
      {
        label: <IntlMessages id="assetCategories.pageHeader" />,
        description: data.asset_category
          ? data.asset_category.asset_category_name
          : null
      },
      {
        label: <IntlMessages id="assetTypes.pageHeader" />,
        description: data.asset_type ? data.asset_type.asset_type_name : null
      },
      {
        label: <IntlMessages id="locations.pageHeader" />,
        description: data.location ? data.location.location_name : null
      },
      {
        label: <IntlMessages id="departments.pageHeader" />,
        description: data.department ? data.department.department_name : null
      },
      {
        label: <IntlMessages id="fixedAssetCode" />,
        description: <strong>{data.fixed_asset_code} </strong>
      },
      {
        label: <IntlMessages id="fixedAssetName" />,
        description: <strong>{data.fixed_asset_name} </strong>
      },
      {
        label: <IntlMessages id="fixedAssets.add.serialNo" />,
        description: data.serial_no
      },
      {
        label: <IntlMessages id="acquisitionDate" />,
        description: data.acquisition_date
          ? moment(data.acquisition_date, "YYYY-MM-DD HH:mm:ss").format(
              "DD MMMM YYYY HH:mm:ss"
            )
          : null
      },
      {
        label: <IntlMessages id="usageDate" />,
        description: data.usage_date
          ? moment(data.usage_date, "YYYY-MM-DD HH:mm:ss").format(
              "DD MMMM YYYY"
            )
          : null
      },
      {
        label: <IntlMessages id="depreciationEndDate" />,
        description: data.depreciation_end_date
          ? moment(data.depreciation_end_date, "YYYY-MM-DD HH:mm:ss").format(
              "DD MMMM YYYY"
            )
          : null
      }
    ];

    const rightSide = [
      {
        label: <IntlMessages id="assetTypes.add.depreciationMethod" />,
        description:
          data.depreciation_method === 1 ? (
            <IntlMessages id="Non Depreciable" />
          ) : data.depreciation_method === 3 ? (
            <IntlMessages id="Double Declining" />
          ) : (
            <IntlMessages id="Straight Line" />
          )
      },
      {
        label: <IntlMessages id="estimatedlife" />,
        description:
          data.estimated_life_year +
          " " +
          getLanguage({ id: "year" }) +
          " " +
          data.estimated_life_month +
          " " +
          getLanguage({ id: "month" })
      },
      {
        label: <IntlMessages id="depreciationRate" />,
        description: (
          <>
            <NumberFormat value={data.depreciation_rate} />
            {"%"}
          </>
        )
      },
      {
        label: <IntlMessages id="intangible" />,
        description:
          data.intangible === 1 ? (
            <IntlMessages id="yes" />
          ) : (
            <IntlMessages id="no" />
          )
      },
      {
        label: <IntlMessages id="assetTypes.add.assetAccount" />,
        description:
          data.account_code_asset + " (" + data.account_name_asset + ")"
      },
      {
        label: <IntlMessages id="assetTypes.add.depreciationAccount" />,
        description:
          data.account_code_depreciation +
          " (" +
          data.account_name_depreciation +
          ")"
      },
      {
        label: <IntlMessages id="assetTypes.add.expenseAccount" />,
        description:
          data.account_code_expense + " (" + data.account_name_expense + ")"
      },
      {
        label: <IntlMessages id="quantity" />,
        description: <NumberFormat value={data.qty} />
      },
      // {
      //   label: <IntlMessages id="salvageValue" />,
      //   description:  <NumberFormat value={data.salvage_value} />
      //               .toFixed(2)
      //               .replace(/\B(?=(\d{3})+(?!\d))/g, ",")
      // },
      {
        label: <IntlMessages id="monthlyDepreciation" />,
        description: <NumberFormat value={data.monthly_depreciation} />
      },
      {
        label: <IntlMessages id="valuePerQuantity" />,
        description:
          data.qty > 0 ? (
            <NumberFormat value={parseFloat(data.book_value / data.qty)} />
          ) : (
            0
          )
      },
      {
        label: <IntlMessages id="assetValue" />,
        description: <NumberFormat value={data.asset_value} />
      },
      {
        label: <IntlMessages id="accumulationOfDepreciation" />,
        description: <NumberFormat value={data.depreciation_value} />
      },
      {
        label: <IntlMessages id="bookValue" />,
        description: <NumberFormat value={data.book_value} />
      }
    ];

    const signatureData = [
      {
        label: <IntlMessages id="createdBy" />
      },
      {
        label: <IntlMessages id="confirmedBy" />
      }
    ];
    const fileName = getLanguage({ id: "fixedAssetDetailReports.pageHeader" })
    return (
      <>
        <PrintTransaction
          className="print-transaction"
          componentRef={ref => (this.componentRef = ref)}
          title={<IntlMessages id="fixedAssetDetailReports.pageHeader" />}
          subtitle={
            <>
              <Row>
                <Col>
                  <strong>{data.transaction_no}</strong>
                </Col>
              </Row>
              <Row style={{ marginTop: "10px" }}>
                <Col span={24}>
                  <PrintField
                    bold={true}
                    label={<IntlMessages id="date" />}
                    description={
                      data.transaction_date
                        ? moment(
                            data.transaction_date,
                            "YYYY-MM-DD HH:mm:ss"
                          ).format("DD MMMM YYYY, HH:mm:ss")
                        : null
                    }
                  />
                </Col>
              </Row>
            </>
          }
        >
          {this.props.data ? (
            <>
              {/* Bagian list detail */}
              <Row style={{ marginTop: "24px" }}>
                <FieldCol leftSide={leftSide} rightSide={rightSide} />
              </Row>

              {/* Expenditure */}
              <Row style={{ marginTop: "24px" }}>
                <Divider
                  orientation="left"
                  style={{
                    fontSize: "12px",
                    alignItems: "center",
                    display: "flex"
                  }}
                >
                  <IntlMessages
                    id="expenditureDetails"
                    style={{ marginTop: "-10" }}
                  />
                </Divider>
                <Col span={24}>
                  <PrintTable
                    columns={expenditureColumns}
                    dataSource={fixed_asset_expenditures}
                    rowKey={row => row.id_fixed_asset_expenditure}
                    pagination={false}
                  />
                </Col>
              </Row>

              {fixed_asset_depreciations.length > 0 ? (
                <Row style={{ marginTop: "24px" }}>
                  <Divider
                    orientation="left"
                    style={{
                      fontSize: "12px",
                      alignItems: "center",
                      display: "flex"
                    }}
                  >
                    <IntlMessages
                      id="fixedAssets.tab.depreciation"
                      style={{ marginTop: "-10" }}
                    />
                  </Divider>
                  <Col span={24}>
                    <PrintTable
                      columns={depreciationColumns}
                      dataSource={fixed_asset_depreciations}
                      rowKey={row => row.id_fixed_asset_depreciation}
                      pagination={false}
                    />
                  </Col>
                </Row>
              ) : null}

              {fixed_asset_disposals.length > 0 ? (
                <Row style={{ marginTop: "24px" }}>
                  <Divider
                    orientation="left"
                    style={{
                      fontSize: "12px",
                      alignItems: "center",
                      display: "flex"
                    }}
                  >
                    <IntlMessages
                      id="fixedAssets.tab.revaluation"
                      style={{ marginTop: "-10" }}
                    />
                  </Divider>
                  <Col span={24}>
                    <PrintTable
                      columns={disposalColumns}
                      dataSource={fixed_asset_disposals}
                      rowKey={row => row.id_fixed_asset_revaluation}
                      pagination={false}
                    />
                  </Col>
                </Row>
              ) : null}

              {fixed_asset_tranfers.length > 0 ? (
                <Row style={{ marginTop: "24px" }}>
                  <Divider
                    orientation="left"
                    style={{
                      fontSize: "12px",
                      alignItems: "center",
                      display: "flex"
                    }}
                  >
                    <IntlMessages
                      id="fixedAssets.tab.transfer"
                      style={{ marginTop: "-10" }}
                    />
                  </Divider>
                  <Col span={24}>
                    <PrintTable
                      columns={transferColumns}
                      dataSource={fixed_asset_tranfers}
                      rowKey={row => row.id_fixed_asset_transfer}
                      pagination={false}
                    />
                  </Col>
                </Row>
              ) : null}

              {fixed_asset_maintenances.length > 0 ? (
                <Row style={{ marginTop: "24px" }}>
                  <Divider
                    orientation="left"
                    style={{
                      fontSize: "12px",
                      alignItems: "center",
                      display: "flex"
                    }}
                  >
                    <IntlMessages
                      id="fixedAssets.tab.maintenances"
                      style={{ marginTop: "-10" }}
                    />
                  </Divider>
                  <Col span={24}>
                    <PrintTable
                      columns={maintenanceColumns}
                      dataSource={fixed_asset_maintenances}
                      rowKey={row => row.id_fixed_asset_maintenance}
                      pagination={false}
                    />
                  </Col>
                </Row>
              ) : null}

              <Row style={{ marginTop: "24px" }}>
                <Col span={24}>
                  <Signature data={signatureData} />
                </Col>
              </Row>
            </>
          ) : null}
        </PrintTransaction>

        <ReactToPrint
          trigger={() => (
            <ButtonPrint
              loading={loading}
              disabled={this.props.data ? false : true}
            />
          )}
          content={() => this.componentRef}
          onBeforePrint={() => (document.title = getFileName(fileName, null, false))}
          onAfterPrint={() => document.title = siteConfig.documentTitle}
          pageStyle={
            "@page { size: landscape; }" +
            "body {" +
            "-webkit-print-color-adjust: exact;" +
            "} "
          }
        />
      </>
    );
  }
}

export default Print;
